import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"

// 要修改 id！改成这个类别下的第一个mdx文件的id
export const query = graphql`
  query GitPageQuery {
    mdx(id: { eq: "1a90cdeb-918c-56f9-9d66-2aad9fc7bcdf" }) {
      body
      id
      tableOfContents(maxDepth: 2)
      frontmatter {
        title
        date
        description
      }
    }
  }
`

const GitIndexPage = ({ data }) => {
  // 左侧目录数据
  const sidebarData = JSON.parse(localStorage.getItem(`sidebar-git`))

  // 中间内容
  const contentData = data.mdx

  // 右侧目录
  const catalogue = contentData.tableOfContents.items || []

  const seo = {
    title: data.mdx.frontmatter.title,
    description: data.mdx.frontmatter.description,
  }

  return (
    <Layout
      sidebarData={sidebarData}
      contentData={contentData}
      catalogue={catalogue}
      seo={seo}
    ></Layout>
  )
}

export default GitIndexPage
